<i18n>
    {
    "zh": {
    "header": {"lang": "语言","title": "WATCHDOG","logout": "登陆"}
    },
    "en": {
    "header": {"lang":"Language","title": "Welike monitor platform","logout":"Logout"}
    }
    }
</i18n>

<template>
    <div class="header" style="text-align: right">
        <span class="title float-left" style="color: #fff;font-weight: bold;font-size: 22px">{{$t('header.title')}}</span>
        <span class="logout float-right pointer" @click="logout">{{$t('header.logout')}}</span>
        <el-select v-model="form.pjValue" @change="selectPj">
            <el-option
                    v-for="(item,index) in pjList"
                    :key="index"
                    :label="item.item"
                    :value="item.item">
            </el-option>
        </el-select>
        <el-select v-model="form.prValue" @change="selectPr">
            <el-option
                    v-for="(item,index) in prList"
                    :key="index"
                    :label="item"
                    :value="item">
            </el-option>
        </el-select>
        <!--<el-row>-->
            <!--&lt;!&ndash;<el-col :span="6">&ndash;&gt;-->
                <!--&lt;!&ndash;<div class="grid-content bg-purple-dark">&ndash;&gt;-->
                    <!--&lt;!&ndash;<el-dropdown @command="handleCommand" class="float-right">&ndash;&gt;-->
                        <!--&lt;!&ndash;<span class="el-dropdown-link">{{$t('header.lang')}}&ndash;&gt;-->
                            <!--&lt;!&ndash;<i class="el-icon-arrow-down el-icon&#45;&#45;right"></i>&ndash;&gt;-->
                        <!--&lt;!&ndash;</span>&ndash;&gt;-->
                        <!--&lt;!&ndash;<el-dropdown-menu slot="dropdown">&ndash;&gt;-->
                            <!--&lt;!&ndash;<el-dropdown-item command="en">EN</el-dropdown-item>&ndash;&gt;-->
                            <!--&lt;!&ndash;<el-dropdown-item command="zh">ZH</el-dropdown-item>&ndash;&gt;-->
                        <!--&lt;!&ndash;</el-dropdown-menu>&ndash;&gt;-->
                    <!--&lt;!&ndash;</el-dropdown>&ndash;&gt;-->
                <!--&lt;!&ndash;</div>&ndash;&gt;-->
            <!--&lt;!&ndash;</el-col>&ndash;&gt;-->
    </div>
</template>

<script>
  import axios from 'axios'
  import cookie from 'js-cookie/src/js.cookie'

  export default {
    name: 'layout-header',
    data () {
      return {
        form: {
          pjValue: '',
          prValue: ''
        },
        pjList: [],
        prList: []
      }
    },
    methods: {
      logout () {
        cookie.remove('token')
        this.$router.push({path: '/sign'})
      },
      // handleCommand (lang) {
      //   if (lang !== window.localStorage.getItem('lang')) {
      //     window.localStorage.setItem('lang', lang)
      //     window.location.reload()
      //   }
      // },
      selectPj (val) {
        this.prList = this.pjList.find(item => { return item.item === val }).arr
        if (val !== window.localStorage.getItem('pjValue')) {
          window.localStorage.setItem('pjValue', val)
        }
      },
      selectPr (val) {
        if (val !== window.localStorage.getItem('prValue')) {
          window.localStorage.setItem('prValue', val)
        }
        this.$notify.success({
          title: '修改环境: 成功！'
        })
        // this.$router.reload()
      }
    },
    mounted () {
      // this.form.pjValue = 'welike'
      // this.form.prValue = 'prod'
      axios.get('profile/getProfile').then(res => {
        for (let key in res.data) {
          this.pjList.push({item: key, arr: res.data[key]})
        }
        const pjV = window.localStorage.getItem('pjValue')
        const prV = window.localStorage.getItem('prValue')
        if (pjV && pjV.length > 0) {
          this.form.pjValue = pjV
        }
        if (prV && prV.length > 0) {
          this.form.prValue = prV
        }
        this.prList = this.pjList.find(item => { return item.item === this.form.pjValue }).arr
        window.localStorage.setItem('prList', this.prList)
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .logout {
        margin-left: 15px;
        font-size: 14px;
        color: #409EFF;
    }
</style>



