<i18n>
    {
    "zh": {
        "menu": {
            "homePage": "首页",
            "serviceTrack": "数据追踪",
            "serviceMonitor": "监控中心",
            "config": "配置中心",
            "serviceConfig": "服务配置",
            "warnConfig": "报警配置",
            "manageCenter":"管理中心",
            "appManage":"权重管理"
        }
    },
    "en": {
        "menu": {
            "homePage": "Home",
            "serviceTrack": "Service Track",
            "serviceMonitor": "Service Monitor",
            "config": "Config center",
            "serviceConfig": "Service Config",
            "warnConfig": "Warn Config",
            "manageCenter":"Management Center",
            "appManage":"App Management"
            }
        }
    }
</i18n>

<template>
    <el-menu background-color="#545c64"
             text-color="#fff"
             active-text-color="#ffd04b" ref="menu">
        <el-menu-item index="home" @click.native="toPage('/index/home')">
            <span class="router-link" name="home">{{$t('menu.homePage')}}</span>
        </el-menu-item>

        <!--<el-submenu index="2">-->
            <!--<template slot="title">数据监控</template>-->
            <!--<el-menu-item index="application" @click.native="toPage('/index/application')">-->
                <!--<span class="router-link" name="application">ZIPKIN</span>-->
            <!--</el-menu-item>-->

            <!--<el-menu-item index="interface" @click.native="toPage('/index/interface')">-->
                <!--<span class="router-link" name="interface">接口流量</span>-->
            <!--</el-menu-item>-->
            <!--<el-menu-item index="top" @click.native="toPage('/index/top')">-->
                <!--<span class="router-link" name="top">数据榜单</span>-->
            <!--</el-menu-item>-->
            <!--<el-menu-item index="topology" @click.native="toPage('/index/topology')">-->
                <!--<span class="router-link" name="topology">服务依赖</span>-->
            <!--</el-menu-item>-->

        <!--</el-submenu>-->
        <el-submenu index="3">
            <template slot="title">基础监控</template>

            <el-menu-item index="3-1" @click.native="toPage('/index/boot')">
                <span class="router-link" name="boot">服务监控</span>
            </el-menu-item>
            <el-menu-item index="3-2" @click.native="toPage('/index/flow')">
                <span class="router-link" name="flow">流量监控</span>
            </el-menu-item>
            <el-menu-item index="3-3" @click.native="toPage('/index/register')">
                <span class="router-link" name="register">注册监控</span>
            </el-menu-item>
            <el-menu-item index="3-4" @click.native="toPage('/index/service-config')">
                <span class="router-link" name="service-config">配置监控</span>
            </el-menu-item>
            <el-menu-item index="3-5" @click.native="toPage('/index/version')">
                <span class="router-link" name="version">版本监控</span>
            </el-menu-item>
        </el-submenu>
        <el-submenu index="4">
            <template slot="title">组件监控</template>
            <el-menu-item index="4-1" @click.native="toPage('/index/db')">
                <span class="router-link" name="db">Druid</span>
            </el-menu-item>
            <el-menu-item index="4-2" @click.native="toPage('/index/redis')">
                <span class="router-link" name="redis">Redis</span>
            </el-menu-item>
            <el-menu-item index="4-3" @click.native="toPage('/index/kafka')">
                <span class="router-link" name="kafka">Kafka</span>
            </el-menu-item>
        </el-submenu>
        <el-submenu index="5">
            <template slot="title">{{$t('menu.config')}}</template>
            <el-menu-item index="5-1" @click.native="toPage('/index/config')">
                <span class="router-link" name="config">{{$t('menu.serviceConfig')}}</span>
            </el-menu-item>
            <el-menu-item index="5-2" @click.native="toPage('/index/warn')">
                <span class="router-link" name="warn">{{$t('menu.warnConfig')}}</span>
            </el-menu-item>
        </el-submenu>
        <el-submenu index="6">
            <template slot="title">{{$t('menu.manageCenter')}}</template>
            <el-menu-item index="6-1" @click.native="toPage('/index/appManage')">
                <span class="router-link" name="appManage">{{$t('menu.appManage')}}</span>
            </el-menu-item>
        </el-submenu>
        <el-submenu index="7">
            <template slot="title">系统管理</template>
            <el-menu-item index="7-1" @click.native="toPage('/index/sysManage')">
                <span class="router-link" name="sysManage">环境管理</span>
            </el-menu-item>
            <el-menu-item index="7-2" @click.native="toPage('/index/user')">
                <span class="router-link" name="user">用户管理</span>
            </el-menu-item>

        </el-submenu>
    </el-menu>
</template>

<script>
  export default {
    name: 'layout-nav',
    data () {
      return {
        menuMap: {

        }
      }
    },
    mounted () {
      var path = this.$router.currentRoute.path
      this.activeTab(path)
    },
    methods: {
      toPage (path) {
        // if (this.$router.currentRoute.path === path) {
        //   // window.location.reload()
        //   return
        // }
        this.$router.push({path})
        this.activeTab(path)
      },
      activeTab (path) {
        var map = {'/': 'home', '': 'home'}
        var name = map[path] || path.split('/')[2]
        this.menuMap[name] && this.$refs['menu'].open(this.menuMap[name])
        var all = document.querySelectorAll(`span.router-link`)
        Array.from(all).forEach(item => {
          item.className = item.className.replace('active', '')
        })
        var el = document.querySelector(`span.router-link[name=${name}]`)
        el.className += ' active '
      }
    }
  }
</script>

<style>
    .el-menu {
        height: 100%;
        width: 100%;
    }

    .router-link {
        color: white;
        text-decoration: none;
    }

    .router-link.active {
        color: #409EFF;
    }
</style>


