<template>
    <el-container class="full-height">
        <el-header>
            <layout-header></layout-header>
        </el-header>
        <el-container>
            <el-aside width="200px">
                <layout-nav></layout-nav>
            </el-aside>
            <el-main>
                <router-view/>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
  import LayoutNav from '@/components/nav'
  import LayoutHeader from '@/components/header'

  export default {
    name: 'index',
    components: {
      LayoutNav,
      LayoutHeader
    }
  }
</script>

<style>
    .full-height {
        height: 100%;
    }

    .el-aside {
        text-align: left;
    }

    .el-header {
        background-color: #b3c0d1;
        color: #333;
        line-height: 60px;
    }
</style>


